.title {
  display: inline-block;
  position: absolute;
  top: 2rem;
  left: 0px;
  right: 0px;
  width: 300px;
  margin: 0 auto;
  font-size: 4rem;
  line-height: 1.2em;
  text-align: center;
  color: rgba(255, 255, 255, 0.75); }

.description {
  display: inline-block;
  position: absolute;
  bottom: 10px;
  left: 10px;
  width: 300px;
  font-size: 1rem;
  line-height: 1.2em;
  text-align: left;
  color: rgba(255, 255, 255, 0.75); }

.target, .hit, .score {
  display: inline-block;
  position: absolute;
  color: red;
  top: 100px;
  left: 10px;
  width: 300px;
  font-size: 1rem;
  line-height: 1.2em;
  text-align: left; }

.target {
  top: 110px; }

.hit {
  top: 135px; }

.score {
  top: 160px; }

.time {
  display: inline-block;
  position: absolute;
  color: yellow;
  top: 0;
  left: 10px;
  width: 200px;
  font-size: 1rem;
  line-height: 1.2em;
  text-align: left; }

.centerBlock {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform: scale(2);
          transform: scale(2); }

.fail, .success {
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2; }
  .fail p, .success p {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 300px;
    margin-left: -250px;
    margin-top: -150px;
    font-size: 3rem;
    line-height: 100px;
    color: red;
    vertical-align: middle;
    text-align: center; }
    .fail p button, .success p button {
      display: inline-block;
      position: relative;
      cursor: pointer;
      margin: 5px;
      width: 150px;
      height: 50px;
      font-size: 2rem;
      background: #00ddeb;
      -webkit-box-shadow: 0px 5px #028d96;
              box-shadow: 0px 5px #028d96;
      color: white;
      border-radius: 3px;
      text-align: center;
      /* so nobody selects the inside text on button */
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      /* Smoothing out hover using css3 transitions, if not, the pressed button effect looks a bit hard */
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
      /* Imitate a pressed effect for buttons */ }
      .fail p button:hover, .success p button:hover {
        background: #01d1de;
        -webkit-box-shadow: 0px 4px #006066;
                box-shadow: 0px 4px #006066;
        color: white; }
      .fail p button:active, .success p button:active {
        top: 4px;
        -webkit-box-shadow: 0px 0px #d3545d;
                box-shadow: 0px 0px #d3545d; }
